  margin-bottom: 1rem;
}
.prose h3 {
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 0.75rem;
}
.prose p {
  margin-bottom: 1.25rem;
  line-height: 1.8;
}
.prose img {
  max-width: 100%;
  height: auto;
  margin: 2rem 0;
  border-radius: 4px;
}
.prose blockquote {
  border-left: 4px solid var(--color-border);
  padding-left: 1.5rem;
  margin: 1.5rem 0;
  font-style: italic;
  color: var(--color-secondary);
}
.prose ul, .prose ol {
  margin: 1rem 0;
  padding-left: 1.5rem;
}
.prose li {
  margin-bottom: 0.5rem;
}
.callout {
  padding: 1rem 1.5rem;
  border-radius: 8px;
  margin: 1.5rem 0;
}
.callout-info {
  background-color: #EBF5FF;
  border-left: 4px solid var(--color-link);
}
.callout-warning {
  background-color: #FFF8E6;
  border-left: 4px solid #F59E0B;
}
.callout-tip {
  background-color: #ECFDF5;
  border-left: 4px solid #10B981;
}
